body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.add-task {
  display: flex;
  align-items: center;
  background-color: white;
  height: 80px;
  margin: 0 10px;
}
.add-task div {
  display: flex;
  flex-direction: column;
  width: 100%;
  padding: 10px;
}

.add-task div label {
  text-align: left;
  color: #0dbaff;
}

.add-task div input {
  border: none;
  border-bottom: 2px solid skyblue;
  padding: 5px 0;
}
.add-task button {
  height: 40px;
  padding: 10px;
  box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.1);
  border: none;
  background: white;
  text-transform: uppercase;
  font-weight: 600;
  cursor: pointer;
}

.add-task div input:focus-visible {
  outline: none;
  border-bottom: 2px solid skyblue;
}

@media screen and (min-width: 568px) {
  .add-task {
    margin: 0 25px;
  }
  .add-task div {
    padding: 0 25px;
  }
}

.backdrop {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 10;
  background: rgba(0, 0, 0, 0.75);
}

.backdrop .modal {
  padding: 25px;
  margin: 10px 26px;
  z-index: 111;
  background-color: white;
  text-align: left;
}

.backdrop .modal .actions {
  text-align: right;
}
.modal .actions button {
  font-size: 1rem;
  margin: 5px;
  padding: 20px;
  text-transform: uppercase;
}

.modal .actions .confirm {
  background-color: #ff4500;
}

.select-list {
  display: flex;
  width: 100%;
  justify-content: space-between;
  text-align: center;
  background-color: #0dbaff;
}

.select-list div {
  width: 100%;
}
.select-list p {
  margin: 0;
  width: 100%;
}
.select-list button {
  width: 100%;
  padding: 15px;
  font-size: 1.1rem;
  border: none;
  cursor: pointer;
  background-color: transparent;
  text-transform: uppercase;
  color: whitesmoke;
}

.select-list svg {
  pointer-events: none;
}
.select-list .selected {
  border-bottom: 2px solid red;
  color: white;
}

.select-list .selected {
  color: white;
  font-weight: bolder;
}

.list-wrapper {
  height: 100%;
}
.list {
  position: relative;
  min-height: 65vh;
}
.list > div {
  margin: 15px;
  padding: 15px;
  border: 1px solid grey;
  border-bottom: 2px dashed grey;
}
.list button {
  cursor: pointer;
}
.list .list-item {
  display: flex;
  padding: 8px 0;
  margin: 8px 0;
}
.list .list-item input {
  border: 2px solid black;
  height: 20px;
}
.list .list-item label {
  cursor: pointer;
}
.list div button {
  cursor: pointer;
  background-color: transparent;
  border: none;
  margin: auto;
}

.list-item button svg {
  pointer-events: none;
  color: black;
  font-size: 1.1rem;
}

.list .list-item input,
label {
  width: 33%;
  font-size: 1.1rem;
}

.toggle-buttons {
  cursor: pointer;
  position: absolute;
  bottom: 20px;
  right: 20px;
  width: 60px;
  height: 60px;
  border-radius: 100%;
  background: skyblue;
  font-size: 1.3rem;
  border: none;
}

.toggle-buttons svg {
  color: white;
}

.toggled {
  background-color: rgb(255 105 156);
}

.App {
  position: relative;
  margin: 0 auto;
  padding: 0 15px;
  text-align: center;
  min-height: 100vh;
}

.header {
  padding: 15px;
  margin-bottom: 0;
  background-color: #3c8cad;
  color: white;
}

.task-done {
  text-decoration: line-through;
}

.delete-all-tasks {
  margin-top: 8px;
  padding: 10px;
  border: none;
  background-color: rgb(255 105 156);
  color: white;
  font-weight: 800;
  text-transform: uppercase;
}

@media screen and (min-width: 568px) {
  .App {
    max-width: 568px;
  }
}

